import axios from "axios";
import React, { Component } from "react";
import Loader from "react-loader-spinner";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import Book from "../../content/book/book";
import s from "./booksByAuthor.module.css";

class BooksByAuthor extends Component {
  state = {
    books: [],
    offset: '1',
    isFetching: true,
  };

  componentDidMount() {
    this.getBooks();
  }

  componentDidUpdate(prevProps) {
    if((prevProps.match.params.query !== this.props.match.params.query) ||  (prevProps.match.params.offset !== this.props.match.params.offset)){
      this.getBooks();
    };
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.props !== nextProps ||
      JSON.stringify(this.state) !== JSON.stringify(nextState)
    );
  }

  toTop(e) {
    window.scrollTo(null, 0);
    e.target.blur()
  }

  getBooks() {
    const tempBooks = [];
    axios
      .get(
        `https://openlibrary.org/search.json?author=${
          this.props.match.params.author
        }&offset=${this.props.match.params.offset * 36 - 36}&limit=36`
      )
      .then((result) => result.data.docs)
      .then((docs) => {
        docs.forEach((item) => { 
          tempBooks.push({
            title: item.title,
            imgUrl: `https://covers.openlibrary.org/b/id/${item.cover_i}-M.jpg`,
            bookId: item.key.match(/\d+/g).join(""),
          });
        });
        this.setState({
          books: [...tempBooks],
          offset: this.props.match.params.offset,
          isFetching: false,
        });
      })
      .catch();
  }

  render() {
    return (
      <>
        <div className={(this.props.darkMode ? s.dark : s.light) + ' ' + s.content}>
          {this.state.isFetching && <Loader type="ThreeDots" color="rgb(130, 185, 0)" />}
          {!this.state.isFetching && this.state.books.map((item, index) => (
            <Book key={index} data={item} darkMode={this.props.darkMode}/>
          ))}
        <div className={(this.props.darkMode && s.dark) +
              " d-flex justify-content-center pagination col-12"}>
          <nav aria-label="Page navigation example">
            <ul className="pagination">
              {this.state.offset !== '1' && (
                <>
                  <li className="page-item">
                    <NavLink
                      onClick={this.toTop}
                      className="page-link"
                      to={`/authors/${this.props.match.params.author}/${
                        +this.state.offset - 1
                      }`}
                      aria-label="Previous"
                    >
                      <span aria-hidden="true">&laquo;</span>
                    </NavLink>
                  </li>
                  <li className="page-item">
                    <NavLink
                      onClick={this.toTop}
                      to={`/authors/${this.props.match.params.author}/${
                        +this.state.offset - 1
                      }`}
                      className="page-link"
                    >
                      {+this.state.offset - 1}
                    </NavLink>
                  </li>
                </>
              )}
              <li className="page-item active activePage">
                <NavLink
                  onClick={this.toTop}
                  to={`/authors/${this.props.match.params.author}/${+this.state
                    .offset}`}
                  className="page-link"
                >
                  {+this.state.offset}
                </NavLink>
              </li>
              <li className="page-item">
                <NavLink
                  onClick={this.toTop}
                  to={`/authors/${this.props.match.params.author}/${
                    +this.state.offset + 1
                  }`}
                  href="#header"
                  className="page-link"
                >
                  {+this.state.offset + 1}
                </NavLink>
              </li>
              <li className="page-item">
                <NavLink
                  onClick={this.toTop}
                  to={`/authors/${this.props.match.params.author}/${
                    +this.state.offset + 1
                  }`}
                  className="page-link"
                  aria-label="Next"
                >
                  <span aria-hidden="true">&raquo;</span>
                </NavLink>
              </li>
            </ul>
          </nav>
        </div>
        </div>
      </>
    );
  }
}

export default withRouter(BooksByAuthor);
