import axios from "axios";
import React, { Component } from "react";
import { withRouter } from "react-router";
import s from "./about-book.module.css";
import defaultCover from "../../images/bookCoverDefault.jpg";
import avatar from "../../images/avatar.jpg";
import { NavLink } from "react-router-dom";
import Book from "../content/book/book";

class AboutBook extends Component {
  state = {
    cover: null,
    author: "",
    description: "",
    title: "",
    id: null,
    authorPhotoId: null,
    authorName: "",
    isFetching: true,
    books: [],
    offset: 1,
  };

  getBooks() {
    axios
      .get(
        `https://openlibrary.org/works/OL${this.props.match.params.bookId}W.json`
      )
      .then((response) => response.data)
      .then((data) => {
        this.setState({
          title: data.title,
          description: data.description,
          id: this.props.match.params.bookId,
          author: data.authors[0].author.key,
          cover: data?.covers && data.covers[0],
        });
        axios
          .get(`https://openlibrary.org${this.state.author}.json`)
          .then((response) => response.data)
          .then((data) => {
            this.setState({
              authorPhotoId: data?.photos ? data.photos[0] : null,
              authorName: data.name,
            });
            const tempBooks = [];
            axios
              .get(
                `https://openlibrary.org/search.json?author=${this.state.author}&offset=0&limit=36`
              )
              .then((result) => result.data.docs)
              .then((docs) => {
                docs.forEach((item) => {
                  tempBooks.push({
                    title: item.title,
                    imgUrl: `https://covers.openlibrary.org/b/id/${item.cover_i}-M.jpg`,
                    bookId: item.key.match(/\d+/g).join(""),
                  });
                });
                this.setState({
                  books: [...tempBooks],
                  offset: this.props.match.params.offset,
                  isFetching: false,
                });
              })
              .catch();
          });
      });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.match.params.bookId !== this.props.match.params.bookId) {
      this.getBooks();
    }
  }

  componentDidMount() {
    this.getBooks();
  }

  render() {
    const splitedName = this.state.authorName.split(" ");
    const searchName = splitedName[splitedName.length - 1];

    return (
      <div
        className={
          s.content +
          " container-lg d-flex flex-wrap " +
          (this.props.darkMode && s.dark)
        }
      >
        <div className="col-12 col-md-4 mt-3">
          <img
            src={
              this.state.cover
                ? `https://covers.openlibrary.org/b/id/${this.state.cover}-L.jpg`
                : defaultCover
            }
            alt="Sorry"
            className={s.coverPhoto + " img-fluid"}
          />
          <div className="mt-3 mb-2">
            <a
              href={`https://openlibrary.org/works/OL${this.props.match.params.bookId}W`}
              target="new"
              className="text-decoration-none fs-5"
            >
              Original Site
            </a>
          </div>
        </div>

        <div className="col-12 col-md-6 offset-md-1">
          <div className="fs-4 mt-3 mb-1">{this.state.title}</div>

          <Description description={this.state.description} />

          <div className="mt-4">
            <div>
              <img
                className={s.authorPhoto}
                src={
                  this.state.authorPhotoId
                    ? ` https://covers.openlibrary.org/b/id/${this.state.authorPhotoId}-M.jpg`
                    : avatar
                }
                alt="Sorry"
              />
            </div>
            <div className="fs-5 mt-1 mb-2">
              Author:
              <NavLink to={`/authors/${searchName}/1`}>
                {" " + this.state.authorName}
              </NavLink>
            </div>
          </div>
        </div>
        <div className='mt-5 d-flex flex-wrap'>
          <h5 className='col-12 mb-5'>More books by author</h5>
          {!this.state.isFetching &&
            this.state.books.map((item, index) => (
              <Book key={index} data={item} darkMode={this.props.darkMode} />
            ))}
        </div>
      </div>
    );
  }
}

const Description = ({ description }) => {
  return (
    <div>
      {!description
        ? "Sorry, We do not have description for this book. For more information about the book click on the link belov cover image"
        : !description.value
        ? description.indexOf("http") === -1
          ? description
          : description.slice(0, description.indexOf("http") - 1)
        : description.value.indexOf("http") === -1
        ? description.value
        : description.value.slice(0, description.value.indexOf("http") - 1)}
    </div>
  );
};

export default withRouter(AboutBook);
