import React, { Component } from "react";
import s from "./header.module.css";
import bookUrl from "../../images/books.png";
import { NavLink } from "react-router-dom";

class Header extends Component {
  render() {
    return (
      <header
        className={
          (this.props.darkMode ? s.darkHeader : s.lightHeader) +
          " container-lg pt-2 pb-2 d-md-flex " +
          s.header
        }
      >
        <div className="col-md-4 col-10 offset-1 offset-md-0 text-center d-flex justify-content-center mb-2 mb-md-0">
          <div>
            <img
              src={bookUrl}
              alt="Some text"
              className={s.bookLogo + " img-fluid"}
            />
          </div>
          <div className={s.name + " fs-4"}>
            <NavLink
              to="/"
              className="text-decoration-none text-reset fw-light"
            >
             Demo Library
            </NavLink>
          </div>
        </div>
        <SearchField />
        <div className="col-md-4 col-10 offset-1 offset-md-0 d-flex">
          <div className="col-8 d-flex justify-content-start justify-content-md-center align-items-center">
            <NavLink
              to="/genres"
              className="text-decoration-none text-reset fs-6 m-1"
            >
              Genres
            </NavLink>
            <NavLink
              to="/authors"
              className="text-decoration-none text-reset fs-6 m-1 ms-3"
            >
              Authors
            </NavLink>
          </div>
          <div className="col-4 d-flex justify-content-md-center justify-content-end align-items-center">
            {this.props.darkMode && (
              <svg
                aria-hidden="true"
                onClick={this.props.setDarkMode.bind(
                  null,
                  !this.props.darkMode
                )}
                className={s.icon}
                data-prefix="fas"
                data-icon="sun"
                role="img"
                viewBox="0 0 512 512"
              >
                <path
                  fill="currentColor"
                  d="M256 160c-52.9 0-96 43.1-96 96s43.1 96 96 96 96-43.1 96-96-43.1-96-96-96zm246.4 80.5l-94.7-47.3 33.5-100.4c4.5-13.6-8.4-26.5-21.9-21.9l-100.4 33.5-47.4-94.8c-6.4-12.8-24.6-12.8-31 0l-47.3 94.7L92.7 70.8c-13.6-4.5-26.5 8.4-21.9 21.9l33.5 100.4-94.7 47.4c-12.8 6.4-12.8 24.6 0 31l94.7 47.3-33.5 100.5c-4.5 13.6 8.4 26.5 21.9 21.9l100.4-33.5 47.3 94.7c6.4 12.8 24.6 12.8 31 0l47.3-94.7 100.4 33.5c13.6 4.5 26.5-8.4 21.9-21.9l-33.5-100.4 94.7-47.3c13-6.5 13-24.7.2-31.1zm-155.9 106c-49.9 49.9-131.1 49.9-181 0-49.9-49.9-49.9-131.1 0-181 49.9-49.9 131.1-49.9 181 0 49.9 49.9 49.9 131.1 0 181z"
                ></path>
              </svg>
            )}
            {!this.props.darkMode && (
              <svg
                aria-hidden="true"
                onClick={this.props.setDarkMode.bind(
                  null,
                  !this.props.darkMode
                )}
                className={s.icon}
                data-prefix="fas"
                data-icon="moon"
                role="img"
                viewBox="0 0 512 512"
              >
                <path
                  fill="currentColor"
                  d="M283.211 512c78.962 0 151.079-35.925 198.857-94.792 7.068-8.708-.639-21.43-11.562-19.35-124.203 23.654-238.262-71.576-238.262-196.954 0-72.222 38.662-138.635 101.498-174.394 9.686-5.512 7.25-20.197-3.756-22.23A258.156 258.156 0 0 0 283.211 0c-141.309 0-256 114.511-256 256 0 141.309 114.511 256 256 256z"
                ></path>
              </svg>
            )}
          </div>
        </div>
      </header>
    );
  }
}

class SearchField extends Component {
  state = {
    field: "",
  };

  onEnterPress = (e)=>{
    if(e.key === 'Enter'){
      e.target.nextSibling.click()
    }
  }

  onInputChange = (e) => {
    this.setState({ field: e.target.value });
  };

  render() {
    return (
      <div className="col-md-4 col-10 offset-1 offset-md-0">
        <div className="input-group">
          <input
            value={this.state.field}
            onChange={this.onInputChange}
            type="text"
            className="form-control"
            placeholder="Search by book title or author"
            aria-label="Recipient's username"
            aria-describedby="button-addon2"
            onKeyPress={this.onEnterPress}
          />
          <NavLink
            to={`/search/${this.state.field.split(" ").join("+")}/1`}
            className="btn btn-primary"
            type="button"
            id="button-addon2"
          >
            Search
          </NavLink>
        </div>
      </div>
    );
  }
}

export default Header;
