import React from "react";
import { NavLink } from "react-router-dom";
import s from "./book.module.css";
import defaultCover from '../../../images/bookCoverDefault.jpg'

const Book = (props) => {

  if(props.data)
  return (
    <div className={s.item + " col-12 col-sm-6 d-flex flex-column col-md-4 col-lg-3"}>
      <div>
        <img src={props.data.imgUrl.match('undefined') ? defaultCover : props.data.imgUrl} className={s.image + " img-fluid " + (props.darkMode && s.dark)} alt='Sorry' />
      </div>
      <div>
        <NavLink to={`/book/${props.data.bookId}`} className={s.navlink + ' ' + (props.darkMode && s.darkLink)}>
          {props.data.title}
        </NavLink>
      </div>
    </div>
  );
  return (
    <div>Sorry</div>
  )
};

export default Book;
