import axios from "axios";
import React, { Component } from "react";
import Loader from "react-loader-spinner";
import Book from "./book/book";
import s from "./content.module.css";
import Slider from "./slider/slider";

class Content extends Component {
  state = {
    startUrl: 90000,
    books: [],
    isFetching: true,
  };

  getBooks() {
    let temp = this.state.startUrl;

    for (let i = 0; i < 52; i++) {
      ((temp) => {
        axios
          .get(`https://openlibrary.org/works/OL${temp}W.json`)
          .then((response) => {
            response.data.covers &&
              this.setState({
                books: [
                  ...this.state.books,
                  {
                    title: response.data.title,
                    imgUrl: `https://covers.openlibrary.org/b/id/${response.data.covers[0]}-M.jpg`,
                    bookId: temp,
                  },
                ],
                isFetching: false,
              });
          })
          .catch();
      })(temp);
      temp += 5;
    }
  }

  componentDidMount() {
    this.getBooks();
  }

  render() {
    return (
      <div
        className={s.content + " " + (this.props.darkMode ? s.dark : s.light)}
      >
        {this.state.isFetching && (
          <Loader type="ThreeDots" color="rgb(130, 185, 0)" />
        )}
        {!this.state.isFetching && (
          <div className={"mb-5 justify-content-center " + s.sliderWrapper}>
            <Slider  />
          </div>
        )}
        <div className="col-12 d-flex flex-wrap">
          {!this.state.isFetching &&
            this.state.books.map((item, index) => (
              <Book key={index} data={item} darkMode={this.props.darkMode} />
            ))}
        </div>
      </div>
    );
  }
}

export default Content;
