import { useState } from "react";
import { HashRouter, Route } from "react-router-dom";
import "./App.css";
import AuthorsList from "./components/authors/authorsList";
import BooksByAuthor from "./components/authors/booksByAuthor/booksByAuthor";
import AboutBook from "./components/book-description/about-book";
import Content from "./components/content/content";
import Footer from "./components/footer/footer";
import BooksByGenre from "./components/genres/booksByGenre/booksByGenre";
import GenresList from "./components/genres/genresList";
import Header from "./components/header/header";
import SearchResult from "./components/searchResult/searchResult";

function App() {
  const [darkMode, setDarkMode]  = useState(false)

  window.darkMode = darkMode

  return (
    <HashRouter basename={ process.env.PUBLIC_URL}>
      <div className="App container-lg">
        <Header darkMode={darkMode} setDarkMode={setDarkMode}/>
        <Route exact path="/" render={() => <Content darkMode={darkMode}/>} />
        <Route path="/book/:bookId" render={() => <AboutBook darkMode={darkMode}/>} />
        <Route path="/search/:query/:offset" render={() => <SearchResult darkMode={darkMode}/>} />
        <Route exact path="/genres" render={() => <GenresList darkMode={darkMode}/>} />
        <Route path="/genres/:genre/:offset" render={() => <BooksByGenre darkMode={darkMode}/>} />
        <Route exact path="/authors" render={() => <AuthorsList darkMode={darkMode}/>} />
        <Route path="/authors/:author/:offset" render={() => <BooksByAuthor darkMode={darkMode}/>} />
        <Footer darkMode={darkMode}/>
      </div>
    </HashRouter>
  );
}

export default App;
